import {Droppable} from "@hello-pangea/dnd";
import styled from "styled-components";
import {IDroppableAreaProps} from "../App";

const Area = styled.div<IDroppableAreaProps>`
    position: relative;
    display: flex;
    justify-content: center;
    align-content: center;
    width: 70px;
    height: 70px;
    border-radius: 35px;
    background-color: ${props => props.isDraggingOver ? "rgba(207,75,0, 0.8)" : props.theme.cardColor};
    margin-top: 30px;
    transition: 0.2s linear;
    svg {
        position: absolute;
        width: 30px;
        height: 30px;
        margin: 0 auto;
        top: 20px;
        fill: rgba(245, 245, 245, 0.8);
    }
`;

function DeleteBox (){
    return <Droppable droppableId="delete">
        {(provided, snapshot) => (
            <Area
                isDraggingOver={snapshot.isDraggingOver}
                isDraggingFromThis={Boolean(snapshot.draggingFromThisWith)}
                ref={provided.innerRef}
                {...provided.droppableProps}
            >
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512"><path d="M136.7 5.9L128 32 32 32C14.3 32 0 46.3 0 64S14.3 96 32 96l384 0c17.7 0 32-14.3 32-32s-14.3-32-32-32l-96 0-8.7-26.1C306.9-7.2 294.7-16 280.9-16L167.1-16c-13.8 0-26 8.8-30.4 21.9zM416 144L32 144 53.1 467.1C54.7 492.4 75.7 512 101 512L347 512c25.3 0 46.3-19.6 47.9-44.9L416 144z"/></svg>
                {provided.placeholder}
            </Area>
        )}
    </Droppable>;
}

export default DeleteBox;